footer{
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}
footer a{
  color: var(--color-bg);
  text-transform: uppercase;
}
.footer_logo{
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}
.permalinks{
  display: flex;
  flex: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer_socials{
  display:flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}
.footer_socials a{
  background-color: var(--color-bg);
  color: var(--color-white);
  padding: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  border-radius: 5px;
}
.footer_socials a:hover{
  background-color: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}
.footer_copyright{
  margin: 4rem;
  color: var(--color-bg);
}
/* ============ MEDIA QUERIES (MEDIUM DEVICES) ======== */
@media screen and (max-width: 1024px) {

}
/* ============ MEDIA QUERIES (SMALL DEVICES) ======== */
@media screen and (max-width: 600px) {
  .permalinks{
      flex-direction: column;
      gap: 1.5rem;
  }   
  .footer_socials{
      margin-bottom: 2.6rem;
  }

}